    --primary-color:rgb(20, 128, 165);
    --primary-color-darker:rgb(9, 29, 56);
}
*{
    box-sizing: border-box;
    outline: 0;
}
body{
    margin: 0;
    padding: 0;
    background-color: #137B9F;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-attachment: fixed;
    background-size: cover;;
    font-family: 'Open sans', sans-serif;
    font-size: 1.1em;
    line-height: 1.5em;
}
.container{
    max-width: 640px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
}
.container h1{
    text-align: center;
}
.senha-gerada{
    font-size: 2em;
    color: var(--primary-color);
    margin: 40px 0;
    text-align: center;
    border: 1px solid;
    padding: 15px;
    border-radius: 5px;

}
input[type="checkbox"]{
   width: 20px; 
   height: 20px;
}
.button{
    display: flex;
    justify-content: center;  
}
.gerar-senha{
    display: flex;
    justify-content: center;
    font-size: 1em;
    min-width: 300px;
    background-color: #137B9F;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-attachment: fixed;
    background-size: cover;
    color: white;
    border: 1px solid #137B9F;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    padding: 7px;
}
.gerar-senha:hover{
    background-image: none;
    background-color: white;
    color: #137B9F;
    border: 1px solid #137B9F;
}

.options p{
    margin:1px;
    margin-bottom: 0px;
    font-size: 20px;
    text-align: center;
    
}
.options input{
    margin-bottom: 5px;
}

